@import '../../assets/css/variable.scss';

@-moz-keyframes tabLite {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(24px);
  }
}

@-webkit-keyframes tabLite {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(24px);
  }
}

@keyframes tabLite {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(24px);
  }
}

@-moz-keyframes tabPro {
  0% {
    transform: translateX(24px);
  }

  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes tabPro {
  0% {
    transform: translateX(24px);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes tabPro {
  0% {
    transform: translateX(24px);
  }

  100% {
    transform: translateX(0px);
  }
}


.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;
  background: #0A172B;

  .menu {
    display: flex;
    transform: inherit;
  }

  .left {
    display: flex;
    align-items: center;

    sup {
      font-size: 12px;
    }

    .logo {
      margin-left: 48px;
      display: flex;
      align-items: center;

      >a {
        color: $whiteFontColor;
        text-decoration: none;
        display: flex;
      }

      .name {
        margin: auto 12px;
        color: $whiteFontColor;
        font-size: $FontSize20;
        // letter-spacing: 0.24em;
        white-space: nowrap;
      }
    }


  }

  .right {
    display: flex;
    align-items: center;
    margin-right: 24px;

    .deri-lite-pro-tab {
      width: 80px;
      height: 24px;
      border-radius: 1000px;
      margin-right: 64px;
      cursor: pointer;

      .pro-lite-check {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 56px;
        height: 24px;
        border-radius: 1000px;
        color: #E0ECFF;
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        background: radial-gradient(303.03% 118.88% at 32.14% 100.00%, #5577FD 0%, #99ADFF 100%);
        box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.15);
        position: relative;
        top: -1px;
        left: -1px;
      }

      .pro-type {
        animation: tabPro .2s .0s linear;
        transform: translateX(0px);
      }

      .lite-type {
        animation: tabLite .2s .0s linear;
        transform: translateX(24px);
        color: #5577FD;
        background: radial-gradient(356.96% 137.44% at 29.46% 100.00%, #CEE0FF 0%, #FFF 100%);
        box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.14);
      }
    }

    .isPro {
      background: linear-gradient(270deg, #203B60 0%, rgba(32, 59, 96, 0.00) 100%);
      border: 1.5px solid #6886FE;
    }

    .isLite {
      background: linear-gradient(268deg, rgba(224, 236, 255, 0.00) 0%, #E0ECFF 100%);
      border: 1px solid #FFF;
    }

    .work-href {
      margin-right: 16px;
    }
  }

  .small-menu {
    margin-left: 24px;
    cursor: pointer;
  }

}

@media only screen and (min-width: 0px) {
  .header {
    .right {
      margin-right: 0px;
    }

    // .left {
    //   .logo {
    //     .name {
    // letter-spacing: 2px;
    //     }
    //   }
    // }
  }
}

@media only screen and (min-width: 1140px) {
  .header {
    .right {
      margin-right: 24px;

    }
  }
}

@-moz-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-100%);
  }
}

//小于760 大于375px
@media only screen and (max-width : 375px) {
  .header {
    .left .logo {
      margin-left: 32px;
    }

    .m-connect {
      width: 130px !important;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      // display: none;

    }
  }
}

@media only screen and (max-width : 760px) {

  .header {
    .left .logo {
      margin-left: 32px;
    }

    .m-connect {
      width: 130px !important;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {

      // display: none;
      #network-select {
        left: calc(100% - 180px) !important;
      }

      .name,
      .network-select-icon {
        display: none;
      }
    }

    .wallet-view {
      .acc-wrapper {
        img {
          margin-right: 0;
        }

        .address {
          display: none;
        }
      }
    }

  }
}

@media only screen and (max-width : 1140px) {

  .header {
    .left .logo {
      margin-left: 32px;

    }

    .right {
      .deri-lite-pro-tab {
        display: none;
      }
    }

    .feed-back {
      display: none;
    }

    .menu {
      display: flex;
      transform: translateX(-100%);
      -moz-animation: menuFadeout 0.2s 0s linear;
      -webkit-animation: menuFadeout 0.2s 0s linear;
      animation: menuFadeout 0.2s 0s linear;
    }

    .small-menu {
      display: block;
    }

    // .lang-select,
    .network-select {
      // display: none;
    }

    .wallet-view {
      margin-right: 8px;
    }

    .lang-select {
      margin-right: 8px;
    }
  }
}

@media only screen and (min-width : 1141px) {
  .header {
    .menu {
      display: flex;
      transform: inherit;
    }

    .small-menu {
      display: none;
    }

    .lang-select,
    .network-select {
      display: flex;
    }
  }
}

