.recharts-tooltip-wrapper {
  pointer-events: auto !important;
}

.info-detail {
  .table {
    .rc-table-cell {
      .liquidate {
        border: 1px solid #ffffff;
        padding: 4px 16px;
        border-radius: 16px;
        color: #ffffff;
      }

      .direction {
        border: 1px solid #93a1b8;
        color: #93a1b8;
        padding: 4px 16px;
        border-radius: 16px;
      }

      .Short,
      .RemoveLiquidity {
        border: 1px solid #ce5e5e;
        color: #ce5e5e;
        padding: 4px 16px;
        border-radius: 16px;
      }

      .Long,
      .AddLiquidity {
        border: 1px solid #59ae99;
        color: #59ae99;
        padding: 4px 16px;
        border-radius: 16px;
      }
    }
  }
}

.info-detail {
  .title {
    display: flex;
  }

  .tab-mining {
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-left: 96px;
    a:hover {
      text-decoration: none;
    }

    .pool-active,
    .pool-unactive {
      box-sizing: content-box;
      padding: 24px 24px;
      line-height: 0;
      font-size: 14px;
      cursor: pointer;
    }

    .pool-active {
      color: #ffffff;
      background: #203b60;
      border-radius: 4px;
    }
  }

  .tip-cursor {
    width: 100px;
    height: 40px;
    border: 1px solid #203b60;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 999;
  }

  // .recharts-legend-wrapper {
  //   top: 0;
  //   height: 0 !important;
  // }

