.tokens {
  .tokens-filter {
    margin-bottom: 50px;
  }
}

@media only screen and (min-width: 500px) {
  .info-detail {
    .recharts-legend-wrapper {
      top: 0;
      height: 0 !important;
    }
  }
}

@media only screen and (max-width: 760px) {
  .info-detail {
    .tab-mining {
      margin-left: 36px;

      .pool-active,
      .pool-unactive {
        font-size: 12px;
      }
    }
  }

  .info-main {
    .list-by-chain {
      .table {
        .rc-table-cell:first-child {
          padding-left: 12px;
        }

        .rc-table-cell {
          min-width: 150px;
          text-align: left;
        }
      }
    }

    .charts {
      .graph {
        min-width: 355px;
      }

      .netValue,
      .tokens {
        .filter {
          .g-button {
            width: 64px;
          }
        }
      }

      .tokens {
        height: 620px;

        .token-list {
          display: flex;
          justify-content: center;
        }
      }
    }
  }
}
