@-moz-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes menuFadein {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes subMenuFadein {
  0% {
    transform: translateX(200%);
  }

  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes subMenuFadein {
  0% {
    transform: translateX(200%);
  }

  100% {
    transform: translateX(0px);
  }
}

@-moz-keyframes subMenuFadein {
  0% {
    transform: translateX(200%);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes subMenuFadeOut {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(200%);
  }
}

@-webkit-keyframes subMenuFadeOut {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(200%);
  }
}

@-moz-keyframes subMenuFadeOut {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(200%);
  }
}

.menu {
  height: 64px;

  .menu-item {
    margin-top: 24px;
    white-space: nowrap;
    height: 100%;

    .sub-item-box {
      position: relative;
      height: 100%;
      cursor: pointer;
      .subMenu-item-box{
        position: absolute;
        min-width: 180px;
        top: 64px;
        background: #203B60;
        font-size: 13px;
        z-index: 99;
      }
      .sub-item{
        height: 100%;
      }
    }

    .legacy {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 42px;
      height: 12px;
      border-radius: 1000px;
      background: #203B60;
      position: relative;
      top: -1px;
      font-size: 10px;
      left: -1px;
    }

    a {
      color: #93A1B8;
      text-decoration: none;
      display: flex;

      img {
        margin-left: 8px;
      }
    }

    .text {
      display: flex;
      border: 0;
    }

  }

  .feed-back {
    display: none;
  }


  .eoc-item {
    .tooltip {
      background-color: transparent;
    }
  }

  .deri-fi-box {
    width: 240px;
    height: 160px;
    background-image: url("../../assets/img/deri-fi.png");
    // background-position: 0 -40px;
    background-repeat: no-repeat;
    border-radius: 10px;
    border: 1px solid #FF780E;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;

    .bg-color-box {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 240px;
      height: 160px;
      background: linear-gradient(144.03deg, #FFFFFF -25.43%, rgba(255, 255, 255, 0) 56.81%);
      border-radius: 10px;
    }

    .deri-fi-text {
      width: 240px;
      height: 63px;
      background: #0A172B;
      border-radius: 0px 0px 10px 10px;
      padding: 8px 16px 15px 16px;
      font-size: 14px;
      font-weight: 500;

      .deri-fi-logo-title {
        display: flex;
        font-weight: 700;
        margin-bottom: 4px;

        img {
          margin-right: 8px;
          margin-left: 0;
        }
      }
    }

  }

  .deri-lite-box {
    background-image: url("../../assets/img/deri-lite.svg");
    border: 1px solid #172E4D;
    background-position: 0 0px;

    .bg-color-box {
      background: linear-gradient(144.03deg, rgba(210, 210, 210, 0.6) -25.43%, rgba(208, 208, 208, 0) 56.81%);
    }

    .deri-fi-text {
      background: linear-gradient(0deg, #0A172B 49.21%, rgba(10, 23, 43, 0) 115.87%);
      border-radius: 0px 0px 10px 10px;
    }
  }

  .trade-to-earn-item,
  .docs-item,
  .more-item,
  .trade-item {
    display: flex;
    align-items: center;

    .sub-item {
      display: flex;
      align-items: center;
      position: relative;

      .have-new-page {
        width: 6px;
        height: 6px;
        background-color: #CD7A37;
        border-radius: 50%;
        margin-right: 4px;
        margin-top: 4px;
      }

      .open-sub-item {
        height: 2px;
        width: 100%;
        background-color: #5577FD;
        position: absolute;
        bottom: 0px;
      }
    }

  }

  .docs-item,
  .more-item,
  .trade-item {
    img {
      margin-top: 5px;
    }
  }

  .m-sub-menu-box {
    display: none;
  }

  .trade-to-earn-item,
  .practice-item {
    display: flex;
    width: 130px !important;
    justify-content: space-between;

    a {
      margin-left: 5px;
    }
  }


  .menu-item.selected a {
    color: #E0ECFF;
    cursor: pointer;
    background: #5577FD1A;
  }

  .sub-menu-item {
    width: 100%;
    padding: 16px;
    text-align: left;
    border: 1px solid transparent;
    height: 48px;
    display: flex;
    align-items: center;

    .new-page {
      width: 45px;
      height: 16px;
      border: 1px solid #CD7A37;
      border-radius: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #CD7A37;
      font-weight: 500;
      font-size: 11px;
      margin-left: 12px;
    }
    a{
      width: 100%;
    }
  }

  .sub-menu-item:hover {
    // border: 1px solid #CD7A37;
    border-radius: 4px;
    background: #5577FD1A;
  }

  .sub-menu-item:last-child {
    // margin-bottom: 24px;
  }

  .__react_component_tooltip.show {
    opacity: 1;
    width: 216px;
    padding: 0;
    position: absolute;
  }

  .__react_component_tooltip.show::after {
    content: none;
  }

  .__react_component_tooltip.show.place-right {
    margin: 0px;
  }

  .footer {
    display: none;
  }
}

.powers-item {
  display: flex;

}


@media only screen and (max-width : 375px) {

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }

    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}

@media only screen and (max-width : 760px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;

    .sub-menu-item {
      height: 30px;
    }

    .menu-item {
      .beta-icon {
        width: 42px;
      }
    }

    .back-icon {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .logo {
        margin-left: 0;
      }
    }

    .footer {
      display: none;
    }
  }

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 2;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }
  }
}

@media only screen and (max-width : 1140px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;

    .m-sub-menu-box {
      display: block;
      width: 100%;
      background: #0A172B;


      .m-tab-pro-lite {
        width: 80px;
        height: 24px;
        border-radius: 1000px;
        margin-bottom: 12px;
        position: relative;

        .pro-lite-check {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 56px;
          height: 24px;
          border-radius: 1000px;
          color: #E0ECFF;
          font-size: 14px;
          font-style: normal;
          font-weight: 600;
          background: radial-gradient(303.03% 118.88% at 32.14% 100.00%, #5577FD 0%, #99ADFF 100%);
          box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.15);
          transition: left 0.2s;
          position: absolute;
          top: -1px;
          left: -1px;
        }

        .pro-type {
          left: -1px;
        }

        .lite-type {
          left: 24px;
          color: #5577FD;
          background: radial-gradient(356.96% 137.44% at 29.46% 100.00%, #CEE0FF 0%, #FFF 100%);
          box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.14);
        }

      }

      .pro-box-type {
        background: linear-gradient(270deg, #203B60 0%, rgba(32, 59, 96, 0.00) 100%);
        border: 1.5px solid #6886FE;
      }

      .lite-box-type {
        background: linear-gradient(268deg, rgba(224, 236, 255, 0.00) 0%, #E0ECFF 100%);
        border: 1px solid #FFF;
      }

      .m-sub-menu-item {
        background: #0A172B;
      }

      .m-hide-sub-menu-item {
        display: block;
        width: 100%;
        height: 100vh;
        position: absolute;
        z-index: 9;
        margin-top: 40px;
        animation: subMenuFadeOut .3s .0s linear;
        transform: translateX(200%);
      }

      .m-show-sub-menu-item {
        display: block;
        height: 100vh;
        margin-top: 40px;
        animation: subMenuFadein .3s .0s linear;
        transform: translateX(0px);

        .sub-menu-item {
          padding: 0;
          height: 48px;
        }
      }

      .menu-box {
        display: flex;
        align-items: center;
        width: 100%;
        position: relative;
        background: #0A172B;

        .arrow-left {
          margin-right: 4px;
          margin-top: 2px;
        }

        .menu-box-hr {
          height: 1px;
          background-color: rgba(0, 133, 255, 0.25);
          ;
          width: 100vw;
          position: absolute;
          left: -41px;
          bottom: -20px;
        }

        .menu-box-show {
          display: flex;
          width: 100%;
          justify-content: space-between;
          align-items: center;
        }
      }
    }

    .open-m-sub-menu {
      position: absolute;
      top: 84px;
      z-index: 9;

    }

    .sub-item {
      display: none !important;
    }

    .btn {
      margin: 20px 0px;
      right: 0;
    }

    .feed-back {
      display: flex;
    }


    .btn:last-child {
      width: 110px;
    }

    .menu-item {
      height: 29px;

      .beta-icon {
        width: 42px;
      }
    }

    .back-icon {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;

      .logo {
        margin-left: 0;
      }
    }

    .footer {
      display: none;
    }
  }

  .show-side-menu {
    .menu {
      opacity: 1;
      width: 100%;
      display: flex;
      z-index: 100;
      animation: menuFadein .3s .0s linear;
      transform: translateX(0px);
    }
  }
}

@media only screen and (min-width : 1141px) {
  .menu {
    display: flex;
    justify-content: space-around;
    margin-left: 14px;
    font-size: 16px;
    background: none;
    position: initial;
    flex-direction: row;
    padding: 0px;

    // :nth-child(3) img {
    //   display: none;
    // }

    .back-icon {
      display: none;
    }

    .menu-item.long {
      width: 128px
    }

    .menu-item {
      width: 96px;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;


      .beta-icon {
        position: absolute;
        top: -14px;
        left: 28px;
      }
    }

    .menu-item:hover>a,
    .menu-item:hover>.sub-menu,
    .menu-item.selected>a,
    .menu-item:hover,
    .sub-menu-item:hover>a {
      color: #E0ECFF;
      // cursor: pointer;
    }

    .eoc-item:hover>a {
      color: #E0ECFF;
    }


    .footer {
      display: none;
    }

  }
}

